import { urlArr } from './UploadFiles/UploadFiles';

async function CallApi(url = 'http://localhost:9000/songs') {
	await fetch(url)
		.then((res) => res.text())
		.then((res) => this.setState({ apiResponse: res }))
		.catch((err) => {
			new Error(err);
		});

	await fetch(url, {
		method: 'POST',
		body: JSON.stringify(urlArr),
		headers: {
			'Content-Type': 'application/json',
		},
	})
		.then((res) => res.json())
		.catch((err) => console.error('Error: ', err));
}

export default CallApi;
